.app {
  display: flex;
  flex-direction: column;
}
.main-part {
  display: grid;
  grid-template-columns: 1fr 5fr;
  background: linear-gradient(60deg, #64b3f4 0%, #c2e59c 100%) no-repeat;
  min-height: 92vh;
}
/*------------------------------------------------------------Top Header---------*/
.header {
  background-color: violet;
  text-align: center;
  margin: 0;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: larger;
  color: white;
}
.header h1 {
  margin: 0;
}
/*------------------------------------------------------------------Options part-----------*/
.options-part {
  background-color: white;
  display: flex;
  flex-direction: column;
  min-width: 150px;
  /* align-items: flex-start; */
  /* margin-top: 100px; */
  /* justify-content: center; */
}
.options-part button {
  font-size: large;
  background-color: transparent;
  border: 0;
  /* margin: 7px 0; */
  width: 100%;
  padding: 7px;
  text-align: left;
}
.options-part button:first-child {
  margin-top: 100px;
}

.todo-part {
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.151);
}

.todo-list {
  text-align: left;
}
.todo-list ul {
  list-style-type: none;
  padding-left: 0;
}
@media screen and (max-width: 500px) {
  .main-part {
    grid-template-columns: 6fr;
    min-width: 350px;
  }
  .options-part {
    display: none;
  }
}

/*----------------------------------------------Name & time and drop down menu div---*/
.date-and-dropdown-div {
  display: flex;
  justify-content: space-between;
}
/*---------------------------------------dropdown menu*/
.menu-btn {
  background-color: rgba(119, 119, 119, 0.295);
  color: black;
  padding: 8px 12px;
  font-size: 20px;
  font-weight: bolder;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  border: none;
  width: 130px;
  /* display: none; */
  /* position: relat */
}
.dropdown-menu {
  position: relative;
  display: inline-block;
}
.dropdown-menu:hover .menu-btn {
  background-color: #3e8e41;
}
.menu-content {
  display: none;
  position: absolute;
  /* background-color: #017575; */
  min-width: 100px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
  right: 1px;
}
.menu-content button {
  width: 130px;
  height: 40px;
  background-color: rgb(255, 255, 255);
  border: 0;
  font-size: large;
  backdrop-filter: blur(5px);
}
.menu-content button:hover {
  background-color: grey;
}
@media screen and (min-width: 500px) {
  .menu-btn {
    display: none;
  }
  .menu-content {
    display: none;
  }
}
/*-----------------------------------------------------------------------add task -------------*/
.new-task-div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.new-task-div .new-task-input {
  background-color: rgba(255, 255, 255, 0.774);
  border: 0;
  min-width: 100px;
  width: 900%;
  min-height: 35px;
  border-radius: 2px;
}

.new-task-div .new-task-input:focus {
  background-color: white;
}
.new-task-div .new-task-input::placeholder {
  color: black;
  font-size: larger;
}

.new-task-div .submit-btn {
  width: 80px;
  height: 40px;
  background-color: green;
  border: 0;
  border-radius: 2px;
  color: white;
  font-size: larger;
  margin-left: 10px;
}

/* --------------------------------------------------------------Name and Time----*/
.name-and-time-div {
  margin-bottom: 10px;
}
.name-and-time-div h3 {
  margin: 0;
  font-size: x-large;
}

/* ---------------------------------------------------------------Todo List ------*/
.each-todo-div {
  background-color: rgb(88, 64, 194);
  min-height: 40px;
  padding: 10px;
  margin: 5px 0;
  color: white;
  display: flex;
  align-items: center;
  font-size: larger;
  position: relative;
}
.each-todo-div span {
  margin-left: 10px;
}
/*------------------------------------------------------------------delete button----*/
.each-todo-div button {
  position: absolute;
  right: 5px;
  background: none;
  border: 0;
  background-color: rgb(255, 255, 255);
  height: 40px;
  width: 40px;
}
.each-todo-div button:hover {
  background-color: rgba(245, 245, 245, 0.445);
}
.each-todo-div button img {
  width: 25px;
  height: 25px;
}
.each-todo-div .todo-important-part {
  position: absolute;
  right: 50px;
}
/*-----------------------------------------------------------------completed chekcbox------*/
input + span {
  display: inline-block;
  position: relative;
  /* transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1); */
}

input + span:before {
  transform: scaleX(0);
  transform-origin: center right;
}

/* input:checked + span {
  color: rgba(200, 0, 0, 1);
  background: rgba(255, 189, 182, 0.3);
  transform: scaleX(0);
} */
input:checked + span:after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  /* box-shadow: 0 1px rgba(255, 255, 255, 0.6); */
  margin-top: -0.7em;
  background: black;
  transform-origin: center left;
  animation: strikethrough 1s 0.5s cubic-bezier(0.55, 0, 0.1, 1) 1;
  /* transition: transform 0.5s cubic-bezier(0.55, 0, 0.1, 1); */
}

/* Little hover effect */

/* Keyframes for initial animation */

@keyframes strikethrough {
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
}
